<template>
  <div id="app">
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
import img from './assets/3.jpg'
import { tileArray } from './assets/js/util.js'

export default {
  name: 'app',

  mounted() {
    let canvas = document.getElementById('canvas')
    let ctx = canvas.getContext('2d')
    let app = document.getElementById('app')
    const cw = canvas.width = window.getComputedStyle(app).width.replace('px', '')
    const ch = canvas.height =  window.getComputedStyle(app).height.replace('px', '')


    class TreeNode{
      constructor(options, ctx) {
        this.options = options
        this.ctx = ctx
        this.drawNode()
        // this.children = children
      }
      
      drawNode() {
        this.ctx.beginPath();
        this.ctx.strokeStyle = this.options.border.color;
        this.ctx.lineWidth = this.options.border.width;
        this.ctx.arc(this.options.currentPos.x, this.options.currentPos.y, this.options.radius, 0, Math.PI * 2);
        if (this.options.background.color) {
          this.ctx.fillStyle = this.options.background.color
          this.ctx.fill();
          this.ctx.stroke()
          if (this.options.startPos.x) {
            this.drawLine()
          }
        }
        if (this.options.background.img) {
          this.image = new Image();
          let that = this;
          this.ctx.fill();
          this.image.onload = function () {
            that.ctx.globalCompositeOperation = 'source-atop'
            that.ctx.drawImage(that.image, that.options.currentPos.x - that.options.radius + 2, that.options.currentPos.y - that.options.radius + 2, that.options.radius * 2 - 4, that.options.radius * 2 - 4)
            that.ctx.beginPath();
            that.ctx.strokeStyle = that.options.border.color;
            that.ctx.lineWidth = that.options.border.width;
            that.ctx.globalCompositeOperation = 'source-over'
            that.ctx.arc(that.options.currentPos.x, that.options.currentPos.y, that.options.radius, 0, Math.PI * 2);
            that.ctx.stroke()
          }
          this.image.src = this.options.background.img;
          if (this.options.startPos.x) {
            this.drawLine()
          }
        }
      }

      drawLine() {
        this.ctx.globalCompositeOperation = 'destination-over'
        this.ctx.beginPath();
        this.ctx.moveTo(this.options.currentPos.x, this.options.currentPos.y)
        this.ctx.lineTo(this.options.startPos.x, this.options.startPos.y)
        this.ctx.stroke()
      }
    }

  class Tree{
    constructor(data) {
      this.oldData = JSON.parse(JSON.stringify(data))
      this.data = this.transData(this.oldData)
      this.drawNode(this.data)
    }

    transData (data, startPos) {
      let newData = [];
      for(let [i, v] of data.entries()) {
        let nowData = {data: {}};
        nowData.data.text = v.text;
        nowData.options = {
          currentPos: this.getPos({x: cw/2, y: ch/2}, i, v.level),
          border: {
            width: 1,
            color: '#ff0000'
          },
          background: {
            color: randomColor()
          },
          radius: 20,
          startPos: startPos?startPos:{}
        }
        if (v.children) {
          nowData.children = this.transData(v.children, nowData.options.currentPos, data.length)
        }
        newData.push(nowData)
      }
      return newData;
    }

    getPos(pos, i, level) {
      if (level === 0) {
        return {x: cw/2, y: ch/2}
      }
      return {x: cw/2 - Math.cos(Math.PI / 6 * i) * 100, y: ch/2 - Math.sin(Math.PI / 6 * i) * 100}
    }

    getY(level, len, i) {
      let num; // 获取父节点的个数
      if (level === 0) {
        num = 0;
      } else {
        num = this.getLevelNum(level - 1)
      }
      let y = ch / (num + 1) / (len + 1) * ++i;
      console.log(y)
      return y
    }

    drawNode(arr) {
      for(let [i ,v] of arr.entries()) {
        let node = new TreeNode(v.options, ctx)
        if (v.children) {
          this.drawNode(v.children)
        }
      }
    }

    getLevelNum(level) {
      let num = 0

      let arr = this.getLevelArr(level, this.oldData)
      for(let i of tileArray(arr)) {
        num += parseInt(i)
      }
      return num
    }

    getLevelArr(level, arr) {
      if (Array.isArray(arr)) {
        let levelArr = []
        if (level == 0) {
          return levelArr.push(arr.length)
        }
        if (arr.length) {
          for(let i of arr) {
            if (i.level + 1 == level) {
              levelArr.push(i.children.length)
            } else {
              levelArr.push(this.getLevelArr(level, i.children))
            }
          }
        } else {
          levelArr.push(1)
        }
        return levelArr
      }
    }
  }

  let tree = new Tree([{
    text: 'root',
    level: 0,
    children: [
      {
        text: 'child1',
        level: 1,
        children: [],
      },
      {
        text: 'child2',
        level: 1,
        children: [],
      },
      {
        text: 'child3',
        level: 1,
        children: [],
      },
            {
        text: 'child1',
        level: 1,
        children: [],
      },
      {
        text: 'child2',
        level: 1,
        children: [],
      },
      {
        text: 'child3',
        level: 1,
        children: [],
      },
                  {
        text: 'child1',
        level: 1,
        children: [],
      },
      {
        text: 'child2',
        level: 1,
        children: [],
      },
      {
        text: 'child3',
        level: 1,
        children: [],
      },
                  {
        text: 'child1',
        level: 1,
        children: [],
      },
      {
        text: 'child2',
        level: 1,
        children: [],
      },
      {
        text: 'child3',
        level: 1,
        children: [],
      },
    ]
  }])


  function randomColor () {
    return '#' + (parseInt(Math.random() * 265)).toString(16) + (parseInt(Math.random() * 265)).toString(16) + (parseInt(Math.random() * 265)).toString(16)
  }


  },
}

</script>

